<template>
  <button type="button" class="rm-button" :class="btnClass" @click="onClick">
    <span>{{ text }}</span>
  </button>
</template>

<script>
export default {
  name: "RmButton",
  props: {
    text: {
      type: String,
      default: "我是按鈕"
    },
    type: {
      type: String,
      default: "default"
    },
    size: {
      type: String,
      default: "default"
    },
    round: {
      type: Boolean,
      default: false
    },
    isClickedStyle: {
      type: Boolean,
      default: false
    },
    showClickedStyle: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      btnClass: {},
      clicked: false
    };
  },
  watch: {
    showClickedStyle: function(val) {
      this.$set(this.btnClass, "clicked", val);
    }
  },
  created() {
    this.getTypeClass();

    this.getSizeClass();

    this.btnClass["is-round"] = this.round;
  },
  methods: {
    onClick() {
      this.clicked = !this.clicked;
      if (this.isClickedStyle) this.$set(this.btnClass, "clicked", this.clicked);

      this.$emit('click', this.clicked);
    },
    getTypeClass() {
      this.btnClass["rm-button--" + this.type] = true;
    },
    getSizeClass() {
      if (this.size !== "default") {
        this.btnClass["rm-button--" + this.size] = true;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
$colors: (
  "success": #009688,
  "warning": #de8d15,
  "danger": #e91e63
);

.rm-button {
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 2px solid #dcdfe6;
  color: #606266;
  -webkit-appearance: none;
  text-align: center;
  box-sizing: border-box;
  outline: none;
  margin: 0;
  transition: 0.1s;
  font-weight: 500;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
  transition: all .3s ease;

  &:hover,
  &.clicked {
    @extend .default-style;
  }

  &.rm-button--success {
    @extend .success-style;
  }

  &.rm-button--warning {
    @extend .warning-style;
  }

  &.rm-button--danger {
    @extend .danger-style;
  }
}

.default-style {
  color: #ffffff;
  border-color: #424242;
  background-color: #606266;
}

@each $name, $color in $colors {
  .#{$name}-style {
    color: $color !important;
    border-color: $color !important;

    &:hover {
      color: $color !important;
      border-color: $color !important;
      background-color: #ffffff !important;
    }

    &.clicked {
      color: #ffffff !important;
      border-color: lighten($color, 15%) !important;
      background-color: $color !important;
    }
  }
}

.is-round {
  border-radius: 20px;
  padding: 12px 23px;
}

// size
.rm-button--medium {
  padding: 10px 20px;
  font-size: 14px;
}

.rm-button--small {
  padding: 9px 15px;
  font-size: 12px;
}

.rm-button--mini {
  padding: 7px 15px;
  font-size: 12px;
}
</style>